import React, { useContext, useEffect, useState } from 'react';
import Fade from 'react-reveal/Fade';
import Tilt from 'react-tilt';
import { Row, Col } from 'react-bootstrap';
import PortfolioContext from '../../context/context';
import ProjectImg from '../Image/ProjectImg';
import arraychunk from 'lodash.chunk';

const Project = () => {
  const { projects } = useContext(PortfolioContext);
  const projectRow = arraychunk(projects,2);
  const [isDesktop, setIsDesktop] = useState(false);
  const [isMobile, setIsMobile] = useState(false);

  useEffect(() => {
    if (window.innerWidth > 769) {
      setIsDesktop(true);
      setIsMobile(false);
    } else {
      setIsMobile(true);
      setIsDesktop(false);
    }
  }, []);

  return (
    <section id="projects">

      {projectRow.map((row, rowIndex) => {
          
          return (<div key={rowIndex} className="row">{
            row.map((col, colIndex) => {
              const { title, info, info2, url, img, id } = col;
            
              return (<Col key={id} md={12} lg={6} className="projcard" >
                <Row >
                    <Fade
                      right={isDesktop}
                      bottom={isMobile}
                      duration={1000}
                      delay={100}
                      distance="30px"
                    >
                      <div className="col__image">
                        <a
                          href={url || '#!'}
                          aria-label="Project Link"
                          rel="noopener noreferrer"
                        >
                          <Tilt
                            options={{
                              reverse: false,
                              max: 8,
                              perspective: 1000,
                              scale: 1,
                              speed: 300,
                              transition: true,
                              axis: null,
                              reset: true,
                              easing: 'cubic-bezier(.03,.98,.52,.99)',
                            }}
                          >
                            <div data-tilt className="thumbnail rounded">
                              <ProjectImg alt={title} filename={img} />
                            </div>
                          </Tilt>
                        </a>
                      </div>
                    </Fade>
                  </Row>
                  <Row>
                    <Fade
                      left={isDesktop}
                      bottom={isMobile}
                      duration={1000}
                      delay={50}
                      distance="30px"
                    >
                      <div className="col__text">
                        <h3 className="col__text-title">{title || 'Project Title'}</h3>
                        <div>
                          <p>
                            {info ||
                              'Lorem ipsum dolor sit, amet consectetur adipisicing elit. Excepturi neque, ipsa animi maiores repellendu distinctioaperiam earum dolor voluptatum consequatur blanditiis inventore debitis fuga numquam voluptate architecto itaque molestiae.'}
                          </p>
                          
                        </div>
                        <a
                          rel="noopener noreferrer"
                          className="cta-btn cta-btn--hero"
                          href={url || '#!'}
                        >
                          Learn More
                        </a>
                      </div>
                    </Fade>
                </Row>
                
              </Col>)})
          }</div>)
        }
        )}
    
    
       </section>
       
  );
};

export default Project;
